import React from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faArrowRight } from '@fortawesome/free-solid-svg-icons';

import classes from './SecondaryNav.module.css';

const secondaryNav = (props) => (
	<nav className={classes.SecondaryNav} data-show={props.showNav}>
		<div className={classes.SecondaryNavLinkContainer}>
			<Link className={classes.SecondaryNavLink} to="/about"><FontAwesomeIcon className={classes.SecondaryNavLinkIcon} icon={faArrowRight} />About</Link>
			<Link className={classes.SecondaryNavLink} to="/contact"><FontAwesomeIcon className={classes.SecondaryNavLinkIcon} icon={faArrowRight} />Contact</Link>
		</div>
	</nav>
);

export default secondaryNav;