import React from 'react';
import { Link } from 'react-router-dom';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faEllipsisH, faTimes } from '@fortawesome/free-solid-svg-icons';

import classes from './PrimaryNav.module.css';

const secondaryNav = (props) => {
	let button = <button className={classes.PrimaryNavBtn} onClick={props.showBtnClickedHandler}><FontAwesomeIcon className={classes.PrimaryNavLinkIconEllipsis} icon={faEllipsisH} /></button>;
	if (props.showNav) {
		button = <button className={classes.PrimaryNavBtn} onClick={props.closeBtnClickedHandler}><FontAwesomeIcon className={classes.PrimaryNavLinkIconTimes} icon={faTimes} /></button>
	}
	
	return (
		<nav className={classes.PrimaryNav}>
			<Link className={classes.PrimaryNavLink} to="/">Refractor</Link>
			{button}
		</nav>
	);
};

export default secondaryNav;