import React, { Component } from 'react';

import Aux from '../Aux/Aux';
import classes from './Layout.module.css';
import PrimaryNav from '../../components/Navigation/PrimaryNav/PrimaryNav';
import SecondaryNav from '../../components/Navigation/SecondaryNav/SecondaryNav';

class Layout extends Component {
	state = {
		showSecondaryNav: false
	};
	
	closeSecondaryNav = () => {
		this.setState({showSecondaryNav: false});
	};
	
	showSecondaryNav = () => {
		this.setState({showSecondaryNav: true});
	};
	
	render () {
		return (
			<Aux>
				<PrimaryNav
					showNav={this.state.showSecondaryNav}
					closeBtnClickedHandler={this.closeSecondaryNav}
					showBtnClickedHandler={this.showSecondaryNav} />
				<SecondaryNav showNav={this.state.showSecondaryNav} />
				<main className={classes.Content}>
					{this.props.children}
				</main>
			</Aux>
		);
	}
}

export default Layout;